	module.exports = {"data":{"wordpressPost":{"title":"How To Get Rid Of Dark Circles, Fine Lines And Stress In Just 10 Minutes","content":"<p>Beauty sleep is an actual thing. For real. If you sleep well you will look more beautiful &#8211; it&#8217;s that simple.</p>\n<p>Why? Well, there&#8217;s a number of reasons. Firstly, quality sleep means increased blood flow and that means less under-eye circles and puffiness. Yes! Do it consistently and you&#8217;re also going to be seeing hair that&#8217;s shiny and full of bounce, skin that positively glows, plus fewer fine lines and even wrinkles. #WINNING.</p>\n<p>A good sleep pattern will also boost energy and mood meaning you feel better and you know what that translates into &#8211; yep, looking better too. Yay again.</p>\n<p>The tricky bit is that our hectic modern-day lifestyles can make quality sleep difficult to achieve and sometimes downright impossible. But there&#8217;s good news too. You can change that. And here&#8217;s how.</p>\n<p>Simply follow this 10-minute routine brought to you by celebrity yogi, <a href=\"https://www.instagram.com/mantrasandmangos/?hl=en\">Instagram star </a>and all-round excellent human Mary Kant.</p>\n<figure id=\"attachment_3037\" style=\"width: 300px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-3037\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02144624/JPG-Plaits-300x242.jpg\" alt=\"\" width=\"300\" height=\"242\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02144624/JPG-Plaits-300x242.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02144624/JPG-Plaits.jpg 592w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Yeah, she&#8217;s a yogi but not your average kind. Image: Instagram/@mantrasandmangos</figcaption></figure>\n<p>NO PHONE ZONE<br />\nMost of us know by now that the blue-light screens of devices ruin us, but still use them before bed anyway. I love magazines as much as Instagram for my creative inspiration but if picking up a book or glossy doesn’t appeal to you, try a positive mind exercise.<br />\nGrab a notepad and pen and write down the three best things that happened that day. They don’t need to be significant achievements. Last night one of mine was ‘making mum smile by complimenting her on her lipstick choice’. Not a major milestone, but it doesn’t need to be &#8230; this is about taking pleasure in the little moments that make us happy and remind us of the joys of living. Positive reflection before sleep is a fantastic way to relax a stressed mind.</p>\n<figure id=\"attachment_3039\" style=\"width: 239px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-3039\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02144748/JPG-Rishikesh-239x300.jpg\" alt=\"\" width=\"239\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02144748/JPG-Rishikesh-239x300.jpg 239w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02144748/JPG-Rishikesh.jpg 476w\" sizes=\"(max-width: 239px) 100vw, 239px\" /><figcaption class=\"wp-caption-text\">See, told she was a yogi! Image: Instagram/@mantrasandmangos</figcaption></figure>\n<p>&nbsp;</p>\n<p>A CUP OF CALM<br />\nThere’s a tea for every occasion and that includes sleep. Certain scents promote relaxation and for many this reduced tension and anxiety relief is key for REM. Try teas that contain lavender, camomile, fennel or lemon.<br />\n<a href=\"https://www.thegoodnightco.com.au/collections/tea/products/goodnight-tea\">Goodnight Co</a>. has a wonderful blend or if relaxation is the name of your game – treat yourself to a <a href=\"https://www.ry.com.au/the-physic-garden-relax-me-large-gift-box/11533297.html\">‘Relax Me’ </a>gift box from Physics Garden, which has an abundance of beauty sleep aids including tea, balms, a wheat eye pillow and more.<br />\nOn tip for pre-bed tea drinkers – double check that your blend is naturally caffeine free.</p>\n<figure id=\"attachment_3040\" style=\"width: 239px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-3040\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02145057/JPG-Peonies-239x300.jpg\" alt=\"\" width=\"239\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02145057/JPG-Peonies-239x300.jpg 239w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02145057/JPG-Peonies.jpg 475w\" sizes=\"(max-width: 239px) 100vw, 239px\" /><figcaption class=\"wp-caption-text\">Mary is all about stopping to smell the roses &#8230; well, peonies. Image: Instagram/@mantrasandmangos</figcaption></figure>\n<p>&nbsp;</p>\n<p>SPRAY, RATTLE AND ROLL<br />\nSleep is so essential but we don’t want to be pumping our body with nasties just to get a good night’s rest. For those who haven’t done much research in the natural sleep aid space lately, there are some weird but wonderful options out there – we’re talking what dreams are (literally) made of.<br />\nMagnesium is known for its ability to improve sleep, improve muscle recovery and help with cramping. If I’ve been really active generally or have pushed myself during a workout I use <a href=\"https://www.saltlaboratory.com/products/salt-lab-200ml?gclid=EAIaIQobChMIhJWxyNyF1gIVzAMqCh2X8g3lEAAYASAAEgIlb_D_BwE\">Salt Lab’s Magnesium Oil </a>– it’s an easy spray that can be quickly applied before bed. Most nights I also use <a href=\"https://www.thegoodnightco.com.au/products/sleep-roll-on\">Sleep Roll On</a> by the Goodnight Co – its lavender, cedarwood and coconut oils make it smell divine and you role on to your pulse points, like perfume, to help the calming scents radiate further for a relaxing sleep.</p>\n<p>&nbsp;</p>\n<figure id=\"attachment_3041\" style=\"width: 239px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-3041\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02145235/JPG-Bikini-butt-239x300.jpg\" alt=\"\" width=\"239\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02145235/JPG-Bikini-butt-239x300.jpg 239w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02145235/JPG-Bikini-butt.jpg 474w\" sizes=\"(max-width: 239px) 100vw, 239px\" /><figcaption class=\"wp-caption-text\">Clearly, Mary&#8217;s routine pays off. Seriously &#8211; look at her! Image: Instagram/@mantrasandmangos</figcaption></figure>\n<p>NAMA’SLEEP TONIGHT<br />\nIs there anything yoga can’t do? Answer &#8211; no. There are a few gentle poses that you can do in bed, which will help relieve tension, promote blood flow and switch off your mind – aiding in a restful night. These are also great if you are on your feet a lot during the day and experience stiffness in your legs.<br />\nI like to start with Viparita Karani, otherwise known as the ‘legs up the wall’, except with my headboard. I also spend two minutes each on the cat stretch and child’s pose, which are amazing for getting the blood circulation going ahead of bed. The ‘night time goddess stretch’, so aptly named, is also brilliant for this – for this one, lay on your back facing the ceiling with the soles of your feet pressed together and your knees bent (like a diamond shape) and breathe. Remember to take long, steady breaths as you relax into these poses.</p>\n<p>Mary has teamed up with the beauty site <a href=\"https://www.ry.com.au\">RY.com.au</a> to produce a Natural Edit. Visit the site to learn more about that project and the products Mary chose. And happy sleeping!</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02145057/JPG-Peonies.jpg"},"categories":[{"slug":"lifestyle","name":"Lifestyle"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"9ff1aa0d-ff88-5f9d-8c75-f6fb23869c49"}}

